<template>
	<div class="home">
		<div class="home-content" v-show="!isShow">
			<!--头部-->
			<div :class="$route.params.id == undefined ? 'header-top' : 'header-nav'">
				<div class="mdui-appbar mdui-appbar-fixed">
					<header-top :kfshow.sync="kfshow">
						<img
								width="28"
								height="28"
								src="../../assets/image/icon-menu.png"
								alt="menu"
								class="icon-menu"
								slot="menu"
								@click="showMenuContent"
						>
						<router-link to="/home" slot="title" class="title-img">
							<img :src="siteInfo.logo?(config.cdnbase+siteInfo.logo):''" alt="title" height="19">
						</router-link>
						<img
								width="28"
								height="28"
								src="../../assets/image/logo.png"
								alt="logo"
								class="logo"
								slot="home"
								@click="kfShow()"
						>
						<!--客服-->
					</header-top>
					<div class="mdui-tab mdui-color-theme">
						<router-link
								to="/home"
								class="mdui-btn nav"
								:class="$route.params.id===undefined ? 'active' : ''"
						>{{lang('热门')}}
						</router-link>
						<router-link
								:to="`/home/${firstList.id}`"
								class="mdui-ripple mdui-ripple-white"
								v-for="(firstList,index) of classInfo"
								:key="index"
								:class="firstList.id == $route.params.id ? 'active' : ''"
						>{{firstList.name}}
						</router-link>
					</div>
					<div
							class="select-box mdui-container"
							v-show="$route.params.id === undefined ? false : true"
					>
						<div class="select" @click="select">
							{{secondList}}
							<i class="mdui-icon material-icons" v-html="icon"></i>
						</div>
						<div class="mdui-tab-navport" style="display: flex;" v-show="isSelect">
							<a href="javascript:;" class="mdui-btn nav" @click="getProductAll">
								<img :src="getClassImg?config.cdnbase+getClassImg:''">
								<span>{{lang('全部商品')}}</span>
							</a>
							<a
									href="javascript:;"
									class="mdui-btn nav"
									v-for="(info,index) of getSecondList"
									:key="index"
									@click="getProduct(info.id, info.name)"
							>
								<img :src="info.class_img?config.cdnbase+info.class_img:''">
								<span>{{info.name}}</span>
							</a>
						</div>
					</div>
				</div>
			</div>
			<!--轮播-->
			<div class="swiper-container">
				<div class="swiper-wrapper">
					<div class="swiper-slide blue-slide" v-for="(item,index) of homeProducts.banner" :key="index">
						<a :href="'/detail/'+item.type_id+'/'+item.classgoods_id"><img :src="config.cdnbase+item.head_img"/></a>
					</div>
				</div>
				<div class="pagination"></div>
			</div>
			<!--首页内容-->
			<div class="mdui-container mdui-page-container mdui-m-t-1" v-show="!displayInfo">
				<div class="tip"><span v-if="siteInfo.free_post_price">满{{siteInfo.free_post_price}}免运费</span><span>货到付款</span><span>在线支付</span></div>
				<div v-for="(product, index) of homeProducts.goods" :key="index" class="goodsList">
					<!--楼层-->
					<div class="floor-title" v-if="product[0]">{{product[0].title}}</div>
					<!--商品组件-->
					<div class="floor">
						<product
								v-for="(good,index) of product"
								:key="index"
								class="mdui-m-b-1"
								:detail="good"
						/>
					</div>
				</div>
			</div>
			<!--分类的商品-->
			<div class="mdui-container mdui-page-container mdui-m-t-1 product-list" v-show="displayInfo">
				<!--商品组件-->
				<product
						v-for="(good,index) of productList"
						:key="index"
						class="mdui-m-b-1"
						:detail="good"
				/>
			</div>
			<!--底部-->
			<min-footer v-if="isLoadAbout"/>
			<footer-nav></footer-nav>
		</div>
		<!--分类-->
		<class-list v-if="isShow" :is-show="isShow" @showMenu="closeMenu()" :first-list="classInfo"/>
	</div>
</template>

<script>
  import product from "../../components/commen/product.vue";
  import {home} from "../../lib/goods";
  import {mapState} from "vuex";
  import Swiper from "swiper";

  export default {
    data() {
      return {
        firstLists: "",
        isSelect: false, // 二级菜单是否被选中
        icon: "&#xe5cf;", // 字体图标切换
        productList: [], // 商品列表,
        homeProducts: {
          banner:[],
          goods:[]
        }, // 主页商品
        isShow: false, // 分类页面显示隐藏
        displayInfo: false, // 显示不同的信息
        secondList: "",
        isLoad: true, // 判断是否继续加载,
        id2: "",
        kfshow: false,
        isLoadAbout: false
      };
    },
    async created() {
      this.isLoadAbout = false;
      this.secondList = this.lang("选择二级分类");
      if (this.$route.params.id !== undefined) {
        this.displayInfo = true;
      }
    },
    mounted() {
      this.getHomeInfo();
    },
    methods: {
      closeMenu() {
        this.isShow = false;
      },
      kfShow() {
        this.kfshow = !this.kfshow;
      },
      async getHomeInfo() {
        if (this.$route.params.id) {
          this.productList = await home.get_home_productAll({
            id1: this.$route.params.id
          });
        }
        this.homeProducts = await home.get_home_goods(); // 获取主页信息
        this.isLoadAbout = true;
      },
      select() {
        // 帅选是否被选择
        this.isSelect = !this.isSelect;
        this.isSelect ? (this.icon = "&#xe5ce;") : (this.icon = "&#xe5cf;");
      },
      showMenuContent() {
        // 分类菜单显示与隐藏
        this.isShow = true;
      },
      async getProduct(id, name) {
        // 点击二级分类时获取数据
        this.productList = await home.get_home_productAll({
          id1: this.$route.params.id,
          id2: id
        });
        this.secondList = name;
        this.isSelect = false;
        this.icon = "&#xe5cf;";
        this.isLoad = true;
        document.documentElement.scrollTop = 0;
        this.id2 = id;
      },
      async getProductAll() {
        this.productList = await home.get_home_productAll({
          id1: this.$route.params.id
        });
        this.secondList = this.lang("全部商品");
        this.isSelect = false;
        this.isLoad = true;
        document.documentElement.scrollTop = 0;
        this.id2 = "";
        this.icon = "&#xe5cf;";
      }
    },
    components: {
      product,
    },
    watch: {
      'homeProducts':function(){
        this.$nextTick(()=>{
          if(!this.swiper){
            this.swiper =  new Swiper('.swiper-container',{
              pagination : '.pagination',
              autoplay: 3500,
              loop:true,
              autoHeight: true,
              notNextTick: true,
              observer: true, // 修改swiper自己或子元素时，自动初始化swiper
              observeParents: true, // 修改swiper的父元素时，自动初始化swiper
            })
          }
        })
      },
      async $route(to) {
        // 当路由改变时
        this.isLoadAbout = false;
        if (to.params.id !== undefined) {
          // 主页路由有参数时 即已选分类
          this.productList = await home.get_home_productAll({
            id1: this.$route.params.id
          });
          this.displayInfo = true;
          this.isLoadAbout = true;
        }
        if (to.params.id === undefined) {
          // 当主页没有参数时 获取主页数据
          this.getHomeInfo();
          this.displayInfo = false;
        }
        this.isSelect = false;
        this.icon = "&#xe5cf;";
        this.secondList = this.lang("选择二级分类");
        document.documentElement.scrollTop = 0;
        this.isLoad = true;
        this.isLoadAbout = false;
        this.id2 = "";
      }
    },
    computed: {
      ...mapState(["siteInfo", "classInfo", "language", "config"]),
      lang() {
        return function (src) {
          return this.language ? this.language[src] : "";
        };
      },
      // eslint-disable-next-line vue/return-in-computed-property
      getSecondList() {
        if (this.classInfo) {
          let secondList = this.classInfo.filter(item => {
            return item.id == this.$route.params.id;
          });
          if (secondList.length === 0) return "";
          return secondList[0].subclass;
        }
      },
      // eslint-disable-next-line vue/return-in-computed-property
      getClassImg() {
        if (this.classInfo) {
          let classImg = this.classInfo.filter(item => {
            return item.id == this.$route.params.id;
          });
          if (classImg.length === 0) return "";
          return classImg[0].class_img;
        }
      }
    }
  };
</script>
<style lang="stylus" scoped>
	@media (max-width: 414px) {
		.home .swiper-slide,.swiper-container{
			width: 100%;
			height: 200px !important;
		}
	}
	@media (min-width: 768px){
		.swiper-slide,.swiper-container{
			width: 100%;
			height: 400px !important;
		}
	}

	.mdui-bottom-nav-fixed
		padding-bottom: 40px;

	.mdui-btn:not(.mdui-ripple):active, .mdui-fab:not(.mdui-ripple):active, .mdui-btn:hover, .mdui-fab:hover {
		background #fff !important
	}

	.home
		.header-top
			height: 77px
		.header-nav
			height: 121px
		.mdui-tab::-webkit-scrollbar
			display: none
		.mdui-tab.mdui-color-theme
			margin-left 8px
			min-height: 36px
		.mdui-tab.mdui-color-theme a
			min-height: 36px
			min-width: min-content;
			padding: 0 8px
		.mdui-btn
			width: 0
		.mdui-appbar
			box-shadow none
			background-color: #fff;
			border-bottom: 1px solid #EDEDED
			margin: 0 auto
			max-width: 768px
		.logo, .title-img, .icon-menu
			cursor: pointer
		.active
			color #B4272D
			border-bottom: 3px solid #B4272D
		.select
			height: 44px
			line-height: 44px
			padding: 0 4px
			font-size: 14px
			color: #777
			text-align center
		.mdui-tab-navport
			width: 100%;
			background: #fff;
			display: none;
			padding: 15px 4%;
			box-sizing: border-box;
			flex-wrap: wrap;
			a.nav:nth-last-child(1)
				margin-bottom: 0;
			a.nav:nth-child(2n+1)
				margin-right: 2%;
			a.nav
				flex: initial;
				width: 48%;
				opacity: 1;
				border: 1px solid #DFDEDB;
				border-radius: 8px;
				min-height: initial;
				min-width: initial;
				display: flex;
				align-items: center;
				justify-content: flex-start;
				margin-bottom: 15px;
				height: 44px;
				cursor: pointer;
				padding: 0 15px;
				flex-direction: row;
				& > img
					width: 40px;
					height: 40px;
					margin-right: 6px;
				& > span
					overflow: hidden;
					text-overflow: ellipsis;
					white-space: nowrap;
		.select-item
			height: 44px
			text-align center
			line-height: 44px
			border: 1px solid #DFDEDB;
			border-radius: 8px
			width: 46%
			&:last-child
				margin-left: 6%
		.home-content
			& > .mdui-container
				max-width: 768px
				.floor
					display: flex
					justify-content space-between
					flex-wrap: wrap
					padding 0 2.4%
					.product
						width: 49%
			.tip
				display: flex
				height: 30px
				background-color: #fff
				justify-content space-around
				align-items: center
				span
					font-size: 18px
					color: #f86161
		.product-list
			width: 100%
			display: flex
			justify-content space-between
			flex-wrap: wrap
			.product
				width: 49%
		.floor-title
			height: 50px;
			font-size: 1.3rem;
			letter-spacing: 2px;
			width: 100%
			text-align center
			line-height 50px
			margin: 5px 0
			background url("../../assets/image/home.jpg") no-repeat center center
			background-size 100%
			color: rgb(189, 97, 97)
			background-color: #FFFF;
		.mdui-btn:hover, .mdui-fab:hover
			background-color: #fff;

	@media (min-width: 768px) {
		.home .swiper-slide img {
			width: 100%
			height: 400px
		}

		.swiper-container-autoheight, .swiper-container-autoheight .swiper-slide {
			height: 400px
		}
	}

	@media (max-width: 414px) {
		.home .swiper-slide img {
			width: 100%
			height: 200px
		}

		.swiper-container-autoheight, .swiper-container-autoheight .swiper-slide {
			height: 200px
		}
	}

	.title-img {
		position: absolute;
		left: 45px;
		margin 0 5px
		top: 5px
	}
</style>
